.postSk {
	margin-right: 12px;
	margin-bottom: 30px;
	animation: skeleton 1s ease infinite alternate;
}

@keyframes skeleton {
	to {
		opacity: 0.5;
	}
}

.postSkImg {
	background-color: #999898;
	height: 153px;
	border-radius: 10px;
}

.postSkInfo {
	background-color: #999898;
	height: 30px;
	margin-top: 10px;
	margin-right: 25%;
	border-radius: 10px;
}
.postSkInfoSm {
	background-color: #999898;
	height: 25px;
	margin-top: 10px;
	margin-right: 50%;
	border-radius: 10px;
}
